.scroll{position:fixed;z-index:999;display:flex;align-items:center;justify-content:center;bottom:var(--margin);padding:1rem;color:var(--white-color);background:var(--black-color);transition:all .4s var(--ease);font-size:var(--font-size);font-weight:600;line-height:1em}.scroll.-center{left:50%;color:var(--black-color);background:var(--yellow-color)}.scroll.-right{right:var(--margin)}.scroll.-switch{color:var(--background-color);background:var(--foreground-color)}@media screen and (max-width: 539px){.scroll{bottom:calc(var(--margin)*1.5)}}