header{position:fixed;z-index:999;display:flex;align-items:center;justify-content:center;top:-1px;left:0;width:100%;height:var(--header);transition:translate .4s var(--ease);transform:translateY(-0.1%);font-size:14px;font-weight:500;line-height:1.5em;letter-spacing:-0.01em}header nav{display:flex;align-items:center;justify-content:space-between;width:100%;height:100%;max-width:var(--body);padding:0 var(--margin);margin:0 auto}header nav span:first-child{width:1.25rem;height:1.25rem;border-radius:50%}header nav a{width:100%;height:100%}header nav a:hover{text-decoration:underline}