.content-block{display:grid;grid-template-columns:repeat(8, 1fr);gap:var(--grid-gap);grid-column:1/-1;padding:2rem 0;border-bottom:1px solid var(--border-tint-color);overflow:hidden}.content-block:last-child{border:none}.content-block:has(.-display){padding:0}.content-block .cell{grid-column:span 2}.content-block .cell:has(.-display){grid-column:1/-1;height:calc(4.5em + 1rem);font-size:var(--display-size)}@media screen and (max-width: 1919px){.content-block .cell:has(.-display){height:calc(5.625em + 1rem)}}@media screen and (max-width: 539px){.content-block .cell:has(.-display){height:calc(6.75em + 1rem)}}@media screen and (max-width: 424px){.content-block .cell:has(.-display){height:calc(7.875em + 1rem)}}@media screen and (max-width: 319px){.content-block .cell:has(.-display){height:calc(9em + 1rem)}}.content-block .cell:has(.-display) #quotes{padding:0 .125em;color:var(--background-color);background:var(--foreground-dimmed-color)}@media screen and (max-width: 539px){.content-block .cell:has(.-display) #quotes{padding:0}}.content-block .cell:has(.-display) .-accent{background:var(--foreground-color) !important}.content-block .cell:has(.-display) .cursor{width:0;outline:.05em solid var(--foreground-color)}.content-block .cell:has(.-display) .cursor.-blink{animation:blink .8s infinite}@keyframes blink{50%{outline-color:var(--surface-color)}}.content-block .cell .-txt-br p{margin-bottom:1em}.content-block .cell .-txt-br p:last-child{margin-bottom:0}.content-block .cell .-txt-cap p:last-child{margin-top:var(--font-size);color:var(--foreground-dimmed-color)}.content-block .cell .-list{display:flex;flex-direction:column;align-items:flex-start}.content-block .cell .-list a:hover{color:var(--background-color);background-color:var(--foreground-color);box-shadow:.25rem 0 0 var(--foreground-color),-0.25rem 0 0 var(--foreground-color);border-radius:.125rem}.content-block .cell .-img{max-height:calc(var(--font-size)*24);padding-top:75%;overflow:hidden}.content-block .cell .-img img{position:absolute;top:0;transform:translateY(-12.5%)}@media screen and (max-width: 1919px){.content-block .cell .-img{padding-top:100%}.content-block .cell .-img img{top:0;transform:translateY(0)}}@media screen and (min-width: 1024px)and (max-width: 1279px){.content-block .cell .-img{padding-top:150%}.content-block .cell .-img img{transform:translateY(25%) scale(150%)}}.content-block .cell .-tag{display:flex;flex-direction:column;align-items:flex-start;gap:.375rem}.content-block .cell .-tag li{padding:0 .5em;background:var(--surface-tint-color);border-radius:.25rem;font-family:jbmono,monospace;font-weight:500;font-size:14px;line-height:1.75em;text-transform:lowercase}@media screen and (max-width: 1023px){.content-block .cell{grid-column:span 4}}@media screen and (max-width: 539px){.content-block .cell{grid-column:span 8}}