html{display:flex}body{display:flex;color:var(--foreground-color);background:var(--surface-color)}#root{width:100%;height:100%}main{display:grid;grid-template-columns:repeat(8, 1fr);gap:var(--grid-gap);width:100%;padding:0 var(--margin);overflow:hidden}@media screen and (max-width: 1023px){main{grid-template-columns:repeat(4, 1fr)}}section{grid-column:1/-1;color:var(--foreground-color);background:var(--surface-color)}section:first-child{padding-top:calc(var(--header)*2.5)}section:last-child{padding-bottom:calc(var(--header)*2.5)}section.hero{padding-bottom:2rem}section.content{padding:2rem 0;background:var(--surface-color);box-shadow:calc(var(--margin)*1) 0 0 var(--surface-color),calc(var(--margin)*-1) 0 0 var(--surface-color)}section.footer{padding-top:2rem}section.footer .content-block:last-child{opacity:.4}